      <div class="title">支付宝充值</div>
      
      <div style="width:90%;margin-top:10px;" v-if="browser=='iphone'">
        <el-button icon="el-icon-success" type="success" @click="jumpurl">点击跳转去支付</el-button>
      </div>
      <div class="qrcode-box">
        <div class="qrcode">
          <div id="qrcode" ref="qrcode"></div>
        </div>
        <div class="scantitle">
          <div class="text">
            <i class="iconfont icon-saoyisao"></i>打开支付宝，扫描二维码支付
          </div>
        </div>
      </div>
