<style scoped>
.pay-index {
  width: 100%;
  box-sizing: border-box;
  background: #ffffff;
  display: flex;
  flex-direction: column;
}
.iphone {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.iphone .title {
  margin-top: 3rem;
  font-size: 1.5rem;
  box-sizing: border-box;
}
.iphone h2 {
  font-size: 1.5rem;
  margin-top: 0;
}

.iphone button {
  width: 100%;
  height: 55px;
  font-size: 22px;
  box-sizing: border-box;
}
.iphone .qrcode-box {
  width: 350px;
  height: 430px;
  border: 1px solid #67c23a;
  margin-top: 10px;
