<script>
//import ap from "@/utils/alipayjsapi";
import QRCode from "qrcodejs2";
export default {
  data() {
    return {
      loading: true,
      browser: "",
      cardinfo: {
        name: "",
        card_index: "",
        money: "",
        bank_mark: ""
      }
    };
  },

  created() {   
            this.loading=false
            this.$nextTick(function() {
              this.qrcode(document.location.toString());
            });
            
            var userAgent = navigator.userAgent;
            if ((userAgent.indexOf("iPhone") > -1&&userAgent.indexOf("Alipay") == -1)||(userAgent.indexOf("Alipay") == -1&&userAgent.indexOf("Android") > -1)) {
              this.browser = "iphone";
              // window.location.href = "taobao://www.alipay.com/?appId=09999988&actionType=toCard&sourceId=bill&cardNo=请勿修改***金额&bankAccount="+res.data.name+"&money="+res.data.money+"&amount="+res.data.money+"&bankMark="+res.data.bank_mark+"&bankName=中国建设银行&cardIndex="+res.data.card_index+"&cardNoHidden=true&cardChannel=HISTORY_CARD&orderSource=from";
          
            }else if(userAgent.indexOf("Alipay") > -1){
              this.browser = "iphone";
              window.location.href = "taobao://www.alipay.com/?appId=09999988&actionType=toCard&sourceId=bill&cardNo=请修改***金额&bankAccount=黄朝煌&money=0.1&amount=0.1&bankMark=CIB&bankName=中国建设银行&cardIndex=1904111043516063446&cardNoHidden=true&cardChannel=HISTORY_CARD&orderSource=from"
            }else{
              this.browser = 'pc'
            }
    },
  

  methods: {
    qrcode(url) {
      new QRCode("qrcode", {
        width: 330,
        height: 330, // 高度  [图片上传失败...(image-9ad77b-1525851843730)]
        text: url // 二维码内容
        // render: 'canvas' // 设置渲染方式（有两种方式 table和canvas，默认是canvas）
        // background: '#f0f'
        // foreground: '#ff0'
      });
    },
    jumpurl:function(){
      window.location.href = "taobao://www.alipay.com/?appId=09999988&actionType=toCard&sourceId=bill&cardNo=请修改***金额&bankAccount=黄朝煌&money=0.1&amount=0.1&bankMark=CIB&bankName=中国建设银行&cardIndex=1904111043516063446&cardNoHidden=true&cardChannel=HISTORY_CARD&orderSource=from";
    }
  }
};
</script>
